@import url(https://fonts.googleapis.com/css?family=Ibarra+Real+Nova:400,400i,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,400i&display=swap);
body{font-family:'Open Sans', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:1vw;line-height:1.05;background-color:black;margin:0 0 50px}a{text-decoration:none;color:inherit}.main-title{width:100%;text-align:center;font-size:7rem;line-height:1.2;background-color:white;margin-bottom:0.5%}.page{font-size:1.25rem}.page .page-title{display:block;font-size:4.75rem;line-height:1.2;text-align:center;width:90%;border-radius:20px;margin:auto auto 2%;color:white;cursor:pointer;-webkit-filter:invert(0);filter:invert(0);-webkit-transition:0.2s;transition:0.2s;background-color:black}.page .page-title:hover{-webkit-filter:invert(1);filter:invert(1)}.page .page-body{width:90%;margin:auto;background-color:white;border-radius:30px;padding:50px;box-sizing:border-box}.header1{font-size:3.25rem;margin-bottom:3%}.header2{font-size:2rem;margin-top:5%;margin-bottom:2%}.header3{font-size:1.5rem;margin-top:5%;margin-bottom:2%}.description{margin-right:10%;margin-left:2%;width:80%}.serif{font-family:'Ibarra Real Nova', serif}.serif.bold{font-weight:bold}

.arrow-btn{color:white;margin:1%;overflow:hidden;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;display:flex;-webkit-filter:invert(0);filter:invert(0);-webkit-transition:0.2s;transition:0.2s}.arrow-btn:hover{-webkit-filter:invert(0.75);filter:invert(0.75)}.arrow-btn.border-left{border-bottom-left-radius:10px;border-top-left-radius:10px}.arrow-btn.border-right{border-bottom-right-radius:10px;border-top-right-radius:10px}.arrow-btn .arrow-btn-body{background-color:black;white-space:nowrap;padding:10px}.arrow-btn .arrow-btn-tail{width:20px;background-color:black;-webkit-clip-path:polygon(0 0, 100% 50%, 0 100%);clip-path:polygon(0 0, 100% 50%, 0 100%)}.arrow-btn .arrow-btn-head{-webkit-transform:translate(1px, 0);transform:translate(1px, 0);width:20px;background-color:black;-webkit-clip-path:polygon(100% 0, 0% 50%, 100% 100%);clip-path:polygon(100% 0, 0% 50%, 100% 100%)}.more-info-btn{text-decoration:underline;-webkit-text-decoration-style:dotted;text-decoration-style:dotted;-webkit-text-decoration-color:black;text-decoration-color:black;cursor:pointer;font-style:italic}

.nav-menu{width:100%;display:flex;background-color:white;margin-bottom:1%;justify-content:space-evenly;align-items:center}.nav-menu .nav-btn{margin:0 !important;-webkit-filter:invert(0);filter:invert(0);-webkit-transition:0.2s;transition:0.2s;flex-grow:1;text-align:center;background-color:white;height:50px;display:flex;flex-direction:column;justify-content:center;align-items:center}.nav-menu .nav-btn .text{display:inline}.nav-menu .nav-btn .dragon{display:none;width:20%}.nav-menu .nav-btn:hover{-webkit-filter:invert(1);filter:invert(1)}.nav-menu .nav-btn.active{-webkit-filter:invert(1);filter:invert(1)}.nav-menu .nav-btn.active .dragon{display:inline}.nav-menu .nav-btn.active .text{display:none}

.ability{font-size:1.25rem;width:95%}.ability .header{display:flex;margin-bottom:3%}.ability .header .name{flex-grow:0.9;font-size:3.5rem;width:50%;line-height:1}.ability .header .code{width:75px;text-align:center;padding:15px;font-weight:bold;font-size:2rem;background-color:black;color:white;border-radius:5px}.ability-menu{width:90%;display:flex;justify-content:center;margin:4% auto auto}.ability-menu .ability-icon{-webkit-filter:invert(1);filter:invert(1);padding:5px;width:5%;margin:1%;cursor:pointer;border-radius:10px}.ability-menu .ability-icon .tooltip{width:120px;background-color:white;color:white;text-align:center;padding:5px 0;border-radius:6px;position:absolute;z-index:1;bottom:125%;left:50%;margin-left:-60px;opacity:0}.ability-menu .ability-icon .tooltip::after{content:"";position:absolute;top:100%;left:50%;margin-left:-5px;border-width:5px;border-style:solid;border-color:white transparent transparent transparent}.ability-menu .ability-icon:hover{-webkit-filter:invert(0);filter:invert(0);background-color:white}.ability-menu .ability-icon:hover .tooltip{color:black;opacity:1}.ability-menu .ability-icon.active{-webkit-filter:invert(0);filter:invert(0);background-color:white}.ability-intro .info-block{display:flex}.abmod-table{border-left:black 1px solid;border-right:black 1px solid;border-collapse:collapse;line-height:1.75;width:600px;text-align:center}.abmod-table thead{background-color:black;color:white;font-weight:bold}.abmod-table tr{border-bottom:black 1px solid;border-top:black 1px solid}.abmod-table tr:nth-child(even){background-color:#ddd}.abmod-table tr:hover{background-color:#444;color:white}

